 -->
<template>
  <div class="malfunction">
    <title-header title="报警与故障记录"></title-header>
    <div class="selects">
      <div class="select-titles">
        <el-row :gutter="24">
          <el-col :span="5"><div class="common-item">项目</div></el-col>
          <el-col :span="5"><div class="common-item">告警类型</div></el-col>
          <el-col :span="5"><div class="common-item">开始时间</div></el-col>
          <el-col :span="5"><div class="common-item">结束时间</div></el-col>
          <el-col :span="4"><div class="common-item"></div></el-col>
        </el-row>
      </div>
      <div class="select-content">
        <el-row :gutter="24">
          <el-col :span="5">
            <div class="common-item selsct-common">
              <el-select v-model="queryFilter.place_id" placeholder="请输入项目名称">
                <el-option
                  v-for="item in $store.state.place.all"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </div>
          </el-col>
          <el-col :span="5">
            <div class="common-item selsct-common">
              <el-select v-model="queryFilter.alarm_type" placeholder="请选择事件类型">
                <el-option
                  v-for="item in alarmTypes"
                  :key="item.code"
                  :label="item.display"
                  :value="item.code">
                </el-option>
              </el-select>
            </div>
          </el-col>
          <el-col :span="5">
            <div class="common-item selsct-common">
              <el-date-picker
                v-model="queryFilter.startTime"
                type="datetime"
                placeholder="请选择">
              </el-date-picker>
            </div>
          </el-col>
          <el-col :span="5">
            <div class="common-item selsct-common">
              <el-date-picker
                v-model="queryFilter.endTime"
                type="datetime"
                placeholder="请选择">
              </el-date-picker>
            </div>
          </el-col>
          <el-col :span="4">
            <el-button class="blue" type="primary" @click="handleSearch">查询</el-button>
            <el-button class="blue" type="primary" @click="exportData">导出</el-button>
          </el-col>
        </el-row>
      </div>
    </div>
    <div class="tool">
      <el-button class="opacity" :class="{blue: noticestatus === 3}" type="primary" @click="changenoticestatus(3)">未处理</el-button>
      <el-button class="opacity" :class="{blue: noticestatus === 4}" type="primary" @click="changenoticestatus(4)">正在处理</el-button>
      <el-button class="opacity" :class="{blue: noticestatus === 5}" type="primary" @click="changenoticestatus(5)">已处理</el-button>
    </div>
    <div class="data">
      <iot-table
        :tableHeader="tableHeader"
        :tableData="tableData"
        :pagination="pagination"
        :loading="loading"
        @reloaddata="initData"
      >
        <div slot="col-user_name" slot-scope="scope"><p>{{scope.row.user_name}} {{scope.row.user_phone}}</p></div>
        <div slot="col-alarm_type" slot-scope="scope"><p>{{alarmType(scope.row.alarm_type)}}</p></div>
        <div slot="col-status" slot-scope="scope"><p>{{pgStatus(scope.row.status)}}</p></div>
        <div slot="col-confirm" slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small">确认派单</el-button>
        </div>
      </iot-table>
    </div>
    <make-order ref="makeOrder" v-on:send-success="initData"></make-order>
  </div>
</template>

<script>
import constant from '@/common/constant'
import { pageAllAlarms, exportAlarmsExcel } from '@/api/hiddendanger'
import makeOrder from '@/views/monitor/components/dialogs/makeOrder'
import dateUtil from '@/utils/dateUtil'
import iotTable from '@/components/iotTable'
import TitleHeader from '@/components/titleHeader'
import msgUtil from '@/utils/msgUtil'

export default {
  name: 'Malfunction',
  components: {
    makeOrder,
    iotTable,
    TitleHeader
  },
  mounted() {
    this.initData()
  },
  data() {
    return {
      tableHeader: [
        { prop: 'address', label: '安装位置' },
        { prop: 'raw_info.timestamp', width: 180, label: '报警时间' },
        { prop: 'user_name', width: 180, label: '监管人', type: 'slot' },
        { prop: 'place_name', label: '所属项目' },
        { prop: 'alarm_type', label: '事件类型', type: 'slot' },
        { prop: 'raw_info.mac', label: '设备mac' },
        { prop: 'status', label: '派工状态', type: 'slot' },
        { prop: 'confirm', width: 150, label: '操作', type: 'slot' }
      ],
      loading: false,
      alarmTypes: Object.keys(constant.alarmTypes).map(k => {
        return {
          code: k,
          display: constant.alarmTypes[k]
        }
      }),
      queryFilter: {
        startTime: new Date(Date.now() - 7 * 24 * 60 * 60 * 1000),
        endTime: new Date(),
        place_id: undefined,
        alarm_type: undefined
      },
      project: {
        name: '',
        id: ''
      },
      noticestatus: 3,
      status: 0,
      tableData: [],
      pagination: {
        page: 1,
        per_page: 10,
        total: 0
      }
    }
  },
  computed: {
    // 事件类型
    alarmType() {
      return (status) => {
        return constant.alarmTypes[status]
      }
    },
    // 派工状态
    pgStatus() {
      return (status) => {
        if (status === 0 || status === 3) {
          return `${status === 0 ? '未派工' : '延迟处理'}`
        } else if (status === 4) {
          return '处理完毕'
        } else {
          return `${status === 1 ? '派单中' : '处理中'}`
        }
      }
    }
  },
  methods: {
    handleSearch() {
      if (this.queryFilter.place_id !== '' || this.queryFilter.alarm_type !== '' || this.queryFilter.startTime !== '' || this.queryFilter.endTime !== '') {
        this.initData()
      } else {
        return msgUtil.error('查询条件不能为空')
      }
      if ((this.queryFilter.startTime !== '' && this.queryFilter.endTime !== '') && (this.queryFilter.startTime.getTime() - this.queryFilter.endTime.getTime() > 0)) {
        msgUtil.error('开始时间不能大于结束时间')
        return
      }
    },
    exportData() {
      exportAlarmsExcel(
        Math.ceil(this.queryFilter.startTime.getTime() / 1000),
        Math.ceil(this.queryFilter.endTime.getTime() / 1000),
        this.queryFilter.project ? (this.queryFilter.project.item || this.queryFilter.project).id : undefined,
        `报警与故障记录(${dateUtil.formatCh(Math.ceil(this.queryFilter.startTime.getTime() / 1000))}至${dateUtil.formatCh(Math.ceil(this.queryFilter.endTime.getTime() / 1000))}).xls`,
        this.queryFilter.alarmType ? this.queryFilter.alarmType.code : undefined,
        this.status
      )
    },
    changenoticestatus(status) {
      this.noticestatus = status || 3
      this.status = 0
      this.initData()
    },
    handleClick(row) {
      const data = {
        id: row.id,
        createTime: row.raw_info.timestamp,
        mac: row.raw_info.mac,
        address: row.address
      }
      this.$refs.makeOrder.show(data)
    },
    async initData() {
      this.loading = true
      const params = {
        page: this.pagination.page,
        per_page: this.pagination.per_page,
        start: this.queryFilter.startTime ? Math.ceil(this.queryFilter.startTime.getTime() / 1000) : '',
        end: this.queryFilter.endTime ? Math.ceil(this.queryFilter.endTime.getTime() / 1000) : '',
        notice_status: this.noticestatus
      }
      if (this.queryFilter.place_id !== '') {
        params.place_id = this.queryFilter.place_id
      }
      if (this.queryFilter.alarm_type !== '') {
        params.alarm_type = this.queryFilter.alarm_type
      }
      try {
        const { data } = await pageAllAlarms(params)
        this.tableData = data.items
        this.pagination = {
          total: data.page_info.total,
          page: data.page_info.page,
          per_page: data.page_info.per_page
        }
        this.loading = false
      } catch (error) {
        this.loading = false
        msgUtil.error('查询数据失败')
      }
    }
  }
}
</script>

